body {
  background-color: #6495ed;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
}

main {
  max-width: 800px;
  margin: 0 auto;
}

.search-form {
  width: 300px;
  gap: 5px;
  margin: 20px auto;
  display: flex;
}

input[type="text"] {
  background-color: rgba(204, 204, 204, .7);
  border: 1px solid #ccc;
  border-radius: 5px;
  outline-color: silver;
  flex-grow: 1;
  padding: 5px;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  padding: .5rem 1rem;
}

button[type="submit"]:hover {
  color: #000;
  background-color: gray;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.photo-card {
  width: 250px;
  text-align: center;
  background-color: rgba(204, 204, 204, .7);
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

.photo-card img {
  max-width: 100%;
  border-radius: 5px;
}

.photo-card img:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin: 15px auto;
  padding: .5rem 1rem;
  display: block;
}

.load-more:hover {
  color: #000;
  background-color: gray;
}

.info {
  margin-top: 10px;
  display: flex;
}

.info-item {
  margin: 5px 0;
  font-size: 11px;
}

